import { useStaticQuery, graphql } from 'gatsby';
import { ImageSharp } from 'src/models/image';

interface ReviewsResult {
  allReviewJson: {
    nodes: Array<{
      author: { name: string; profileImg: ImageSharp; role: string };
      title: string;
      description: string;
      url: string;
    }>;
  };
}

export function useMentoringReviews() {
  const {
    allReviewJson: { nodes: reviews },
  } = useStaticQuery<ReviewsResult>(graphql`
    query AllReviewsQuery {
      allReviewJson {
        nodes {
          author {
            name
            profileImg {
              childImageSharp {
                fixed(width: 800, height: 800, quality: 100, cropFocus: CENTER) {
                  ...GatsbyImageSharpFixed_noBase64
                }
                fluid {
                  ...GatsbyImageSharpFluid
                }
              }
            }
            role
          }
          title
          description
          url
        }
      }
    }
  `);

  return reviews;
}
