import React, { useMemo } from 'react';
import { makeStyles, NoSsr, useMediaQuery, useTheme } from '@material-ui/core';
import Rellax from 'react-rellax-wrapper';
import CircleSvg from 'components/Utils/svg/CircleSvg';
import ArcSvg from 'components/Utils/svg/ArcSvg';
import TriangleSvg from 'components/Utils/svg/TriangleSvg';

const useStyles = makeStyles(({ breakpoints }) => ({
  svgWrapper: {
    position: 'absolute',
    top: 0,
    left: 0,
    width: '100%',
  },
  redCircle: {
    position: 'absolute',
    top: 100,
    right: '25%',
    [breakpoints.down('md')]: {
      top: 100,
      right: '10%',
    },
  },
  yellowArc: {
    position: 'absolute',
    top: 180,
    right: '20%',
    transform: 'rotate(-16deg)',
    [breakpoints.down('md')]: {
      top: 350,
      right: '10%',
    },
  },
  greenArc: {
    position: 'absolute',
    top: 750,
    left: '5%',
    transform: 'rotate(29.31deg)',
    [breakpoints.down('md')]: {
      top: 550,
    },
  },
  blueTriangle: {
    position: 'absolute',
    top: 1200,
    right: '40%',
    transform: 'rotate(25.35deg)',
    [breakpoints.down('md')]: {
      top: 750,
    },
  },
}));

const getElementSize = (size: number, isMobile: boolean) => {
  return isMobile === true ? size / 3.5 : size;
};

const SvgElements = () => {
  const styles = useStyles();
  const theme = useTheme();
  const matches = useMediaQuery(theme.breakpoints.down('md'));

  const sizes = useMemo(() => {
    return {
      redCircle: getElementSize(204, matches),
      yellowArc: getElementSize(710, matches),
      greenArc: getElementSize(350, matches),
      blueTriangle: {
        width: getElementSize(80, matches),
        height: getElementSize(100, matches),
      },
    };
  }, [matches]);

  return (
    <NoSsr>
      <div className={styles.svgWrapper}>
        <Rellax speed={0.5}>
          <CircleSvg size={sizes.redCircle} color="#E73C22" className={styles.redCircle} />
        </Rellax>
        <Rellax speed={3}>
          <ArcSvg size={sizes.yellowArc} color="#F1AD25" className={styles.yellowArc} degree={180} />
        </Rellax>
        <Rellax speed={1.5}>
          <ArcSvg size={sizes.greenArc} color="#00C090" className={styles.greenArc} degree={180} />
        </Rellax>
        <Rellax speed={5}>
          <TriangleSvg
            width={sizes.blueTriangle.width}
            height={sizes.blueTriangle.height}
            color="#2248E6"
            className={styles.blueTriangle}
            points={[
              [0.8, 0],
              [1, 0.95],
              [0, 1],
            ]}
          />
        </Rellax>
      </div>
    </NoSsr>
  );
};

export default SvgElements;
