import React from 'react';
import {
  Card,
  CardContent,
  Container,
  Grid,
  makeStyles,
  Typography,
  Avatar,
  CardHeader,
  CardActionArea,
} from '@material-ui/core';
import { useMentoringReviews } from 'src/hooks/useMentoringReview';

const useStyles = makeStyles(({ spacing, typography, breakpoints }) => ({
  root: {
    padding: `${spacing(15)}px 0`,
  },
  title: {
    fontWeight: typography.fontWeightBold,
    textAlign: 'center',
    marginBottom: spacing(2),
    [breakpoints.down('md')]: {
      fontSize: 20,
      marginBottom: spacing(1),
    },
  },
  description: {
    marginBottom: spacing(10),
    textAlign: 'center',
    fontSize: 18,
    [breakpoints.down('md')]: {
      fontSize: 16,
      marginBottom: spacing(4),
    },
  },
  postTitle: {
    fontWeight: 'bold',
  },
}));

const ReviewSection = () => {
  const styles = useStyles();
  const reviews = useMentoringReviews();
  console.log(reviews);

  return (
    <section className={styles.root}>
      <Container>
        <Typography variant="h4" component="h2" className={styles.title}>
          멘토링 후기
        </Typography>
        <Typography className={styles.description}>
          루비콘 멘토링 프로젝트에 참여했던 멘티들이 느꼈던 생생한 경험이 궁금하다면
          <br />
          멘티들이 직접 작성한 후기 포스팅을 한번 읽어보세요.
        </Typography>
        <Grid container spacing={2}>
          {reviews.map(({ title, author, url, description }, index) => (
            <Grid item key={index} xs={6} sm={3}>
              <Card onClick={() => window.open(url)}>
                <CardActionArea>
                  <CardHeader
                    avatar={<Avatar alt={author.name} src={author.profileImg.childImageSharp.fixed?.src} />}
                    title={author.name}
                    subheader={author.role}
                  />
                  <CardContent>
                    <Typography variant="body1" gutterBottom={true} className={styles.postTitle}>
                      {title}
                    </Typography>
                    <Typography variant="body2">{description}</Typography>
                  </CardContent>
                </CardActionArea>
              </Card>
            </Grid>
          ))}
        </Grid>
      </Container>
    </section>
  );
};

export default ReviewSection;
