import React, { useMemo } from 'react';
import { makeStyles, Theme, Typography, Box } from '@material-ui/core';
import { MENTORING_EXPIRY } from 'src/constants';
import FadeUp from 'components/Animated/FadeUp';
import MentoringApplyButton from 'components/Buttons/MentoringApplyButton';
import SvgElements from './SvgElements';
import differenceInCalendarDays from 'date-fns/differenceInCalendarDays';

const useStyles = makeStyles<Theme>(({ typography, breakpoints, spacing }) => ({
  root: {
    height: `100vh`,
    backgroundColor: '#FFFFFF',
    padding: `0 ${spacing(2)}px`,
  },
  contentsArea: {
    zIndex: 1,
  },
  svgArea: {
    zIndex: 0,
  },
  subtitleWrapper: {
    backgroundColor: '#C5E5DD',
    padding: '0 14px',
  },
  subtitle: {
    whiteSpace: 'nowrap',
    fontWeight: typography.fontWeightBold,
    fontSize: 24,
    lineHeight: 1,
    transform: 'translateY(-8px)',
    [breakpoints.down('md')]: {
      fontSize: 16,
    },
  },
  title: {
    fontWeight: typography.fontWeightBold,
    fontSize: 56,
    marginTop: 22,
    lineHeight: '81px',
    [breakpoints.down('md')]: {
      fontSize: 24,
      marginTop: 0,
    },
  },
  description: {
    textAlign: 'center',
    marginTop: 32,
    fontSize: 18,
    [breakpoints.down('md')]: {
      fontSize: 14,
      marginTop: 16,
    },
  },
  cta: {
    marginTop: 78,
    fontSize: 24,
    fontWeight: typography.fontWeightBold,
    padding: '8px 50px',
    [breakpoints.down('md')]: {
      fontSize: 16,
      marginTop: 30,
    },
  },
}));

const today = new Date();
const expiryDiff = differenceInCalendarDays(MENTORING_EXPIRY, today);

interface Props {
  onClickApply?: () => void;
}
const IntroSection = ({ onClickApply }: Props) => {
  const styles = useStyles();
  const dday = useMemo(() => {
    if (expiryDiff === 0) {
      return 'D-Day';
    } else if (expiryDiff < -100) {
      return '';
    } else if (expiryDiff < 0) {
      return '신청 마감';
    } else {
      return `D-${expiryDiff}`;
    }
  }, []);

  return (
    <Box className={styles.root} display="flex" justifyContent="center" alignItems="center">
      <Box display="flex" flexDirection="column" alignItems="center" className={styles.contentsArea}>
        {dday === '' ? null : (
          <div className={styles.subtitleWrapper}>
            <FadeUp delay={0}>
              <Typography variant="h1" className={styles.subtitle}>
                루비콘 실무자 멘토링 프로젝트 {dday}
              </Typography>
            </FadeUp>
          </div>
        )}
        <FadeUp delay={0}>
          <Typography variant="h2" className={styles.title}>
            성장을 통해 나의 가치를 높여보세요.
          </Typography>
        </FadeUp>
        <FadeUp delay={0}>
          <Typography className={styles.description}>
            나름 열심히 공부하고 있는데 내가 맞는 방향으로 가고 있는 걸까요?
            <br />
            동료들과 건설적인 피드백을 주고받으며 성장하고 싶은데 그런 환경을 찾기가 어려운가요?
            <br />
            도대체 어떻게 하면 좋은 제품을 만들 수 있을까요?
            <br />
            <br />
            이런 질문들에 대한 해답을 스스로 찾을 수 있도록, 루비콘이 도와드릴게요!
          </Typography>
        </FadeUp>
        <MentoringApplyButton
          className={styles.cta}
          color="primary"
          variant="contained"
          href="/mentoring/apply-forms/"
          size="large"
          expiry={MENTORING_EXPIRY}
          onClick={onClickApply}
        >
          지금 바로 멘티로 참여하기
        </MentoringApplyButton>
      </Box>
      <div className={styles.svgArea}>
        <SvgElements />
      </div>
    </Box>
  );
};

export default IntroSection;
